
const lerp = (start, end, t) => {
    // If the difference is less than 0.0001 (i.e., within 4 decimal places), snap to end
    if (Math.abs(end - start) < 0.0001) {
      return end;
    }
    return start + (end - start) * t;
};

function calculateScrollOffset(canvasOffset, windowHeightCorrectionFactor = 0) {
    const windowHeight = window.innerHeight + windowHeightCorrectionFactor;
    if((window.scrollY - canvasOffset) > windowHeight) {
        return 1;
    }
    return (window.scrollY - canvasOffset) / windowHeight;
}

function normalize(value, min_value, max_value) {
    if (max_value === min_value) {
        console.warn('Cannot normalize: max and min values are equal.');
        return value; // or throw an error
    }
    return (value - min_value) / (max_value - min_value);
}

export {calculateScrollOffset, lerp, normalize};